* {
   font-family: 'Inter', sans-serif;
}

li {
   list-style: none;
}

input {
   border: none;
}

button {
   border: none;
   cursor: pointer;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p {
   margin: 0;
   padding: 0;
   border: 0;
   font-size: 100%;
   font-weight: normal;
   vertical-align: baseline;
}

.App {
   position: relative;
   display: flex;
   flex-direction: column;
   min-height: 100vh;
}

.wrapper {
   flex: 1 0 auto;
}

.rowBlock {
   display: flex;
   justify-content: space-between;
}

/* Заголовок страницы */
.pageTitle {
   color: var(--black, #212121);
   font-size: 64px;
   font-style: normal;
   font-weight: 500;
   line-height: 72px;
   letter-spacing: -2.56px;
}

/* Фильтр */
.filter {
   display: flex;
   flex-direction: column;
}

.filterBtnContainer {
   display: flex;
   justify-content: space-between;
   width: 306px;
   padding: 4px;
   border-radius: 12px;
   background: #F6F6F6;
}

.filterChoce {
   width: 157px;
   height: 40px;
   text-align: center;
   line-height: 40px;
   border-radius: 8px;
   background: #F6F6F6;
   cursor: pointer;
}

.filterChoce.active {
   background: #EEEE20;
}

.filterThemeContainer {
   position: relative;
   display: flex;
}

.filterThemeContainer:hover .calendar {
   display: block;
}

.filterThemeContainer::after {
   content: '';
   position: absolute;
   top: calc(50% - 12px);
   right: 12px;
   width: 24px;
   height: 24px;
   background: url('./assets/icons/filterInputCalendar.svg') 0 0/auto auto no-repeat;
}

.inputCalendar {
   flex: 1;
   padding: 12px;
   border-radius: 12px;
   background: #F6F6F6;
   color: #212121;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 24px;
}

.filterSearchContainer {
   display: flex;
   position: relative;
}

.filterSearchContainer::after {
   content: '';
   position: absolute;
   top: calc(50% - 12px);
   right: 12px;
   width: 24px;
   height: 24px;
   background: url('./assets/icons/filterInputSearch.svg') 0 0/auto auto no-repeat;
}

.filterSearch {
   flex: 1;
   padding: 12px;
   color: #808080;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 24px;
}

.filterBtnNewsArchive {
   padding: 12px;
   border-radius: 12px;
   border: 1px solid #EDEDED;
   color: #212121;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 24px;
}

.juridicalInfo {
   color: #808080;
   font-size: 14px;
   font-style: normal;
   font-weight: 400;
   line-height: 20px;
}




/* Колонки */
.columnContainer {
   display: flex;
}

.columnLarge {
   width: calc(76% - 10px);
}

.columnSmal {
   width: calc(24% - 10px);
}

/* Граница */
.borderMain {
   padding: 40px;
   border-radius: 40px;
   border: 1px solid #DFDFDF;
}

/* Карточка */
.cardContainer {
   display: flex;
   justify-content: space-between;

}

/* Состояние кнопок */
.btnY {
   background: #EEEE20;
}

.btnY:hover {
   background: #FCFC3B;
}

.btnY:active {
   background: #ECE513;
}

/*  */
.btnW {
   border: 1px solid transparent;
}

.btnW:hover {
   color: #DCDCDC;
   border: 1px solid #EEEE20;
}

.btnW:hover img {
   opacity: .2;
}

.btnW:active {
   color: #303030;
   border: 1px solid #EEEE20;
}

.btnW:active img {
   opacity: 1;
}

