.bgBlock {
   width: 100%;
   height: 142px;
   background: #EEEE20;
   margin-bottom: 29px;
}

.logoRow {
   display: flex;
   align-items: center;
   justify-content: space-between;
   height: 75px;
}

.logoBlock {
   display: flex;
}

.textMain {
   margin-left: 13.33px;
   width: 204px;
}

.titleInfo {
   font-size: 10px;
   font-weight: 400;
   line-height: 14.12px;
   color: #3030308F;
}

.titleAdministration {
   font-size: 10px;
   font-weight: 600;
   line-height: 14.12px;
}

.settingsContainer {
   display: flex;
   align-items: center;
}

.versionVI img {
   margin-right: 8px;
}

.versionVI {
   font-size: 16px;
   font-weight: 400;
   line-height: 24px;
   color: #8C7E00;
   cursor: pointer;
}

.hotLine {
   margin-left: 32px;
   display: flex;
   padding: 8px 16px;
   justify-content: center;
   align-items: center;
   border-radius: 40px;
   background: #FFF;
   border: none;
   color: #303030;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 24px;
   cursor: pointer;
}

.mainMenuContainer {
   width: 100%;
   height: 94px;
   position: absolute;
   top: 77px;
   background: #FFFFFF;
   border-radius: 40px;
}

.mainMenu {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 0 40px;
   margin: 18px 0 28px 0;
   background: #212121;
   border-radius: 16px;
}

.itemsContainer {
   display: flex;
   align-items: center;
}

.item {
   position: relative;
   padding: 12px 0;
   font-size: 16px;
   font-weight: 400;
   line-height: 24px;
   color: #FFFFFF;
   cursor: pointer;
   letter-spacing: 1px;
}

.item:not(:first-child) {
   margin-left: 36px;
}

.itemArrow:hover:after {
   transform: rotate(180deg);
}

.itemArrow {
   margin-right: 24px;
}

.itemArrow:after {
   content: '';
   position: absolute;
   bottom: calc(50% - 12px);
   left: calc(100% + 4px);
   display: inline-block;
   width: 24px;
   height: 24px;
   transition: all ease-in-out .4s;
   background: url('../../assets/icons/arrow_down.svg') 0 0/auto auto no-repeat;
}

.item:hover .dropDownMenu {
   display: block;
}

.item .dropDownMenu {
   display: none;
   position: absolute;
   top: 100%;
   left: -16px;
   font-weight: bold;
   background: #FFFFFF;
   color: black;
   padding: 16px;
   border-radius: 16px;
   box-shadow: 1px 1px 10px black;
   z-index: 999;
}

.linkMenu {
   display: block;
   color: #303030;
   font-size: 16px;
   font-style: normal;
   font-weight: 400;
   line-height: 24px;
   text-decoration: none;
   white-space: nowrap;
}

.linkMenu:hover {
   color: #B0B0B0;
}

.linkMenu:not(:first-child) {
   margin-top: 8px;
}

.searchBlock {
   letter-spacing: 1px;
   font-size: 16px;
   line-height: 24px;
   color: #a0a0a0;
   position: relative;
   padding-left: 32px;
   cursor: pointer;
}

.searchBlock::after {
   content: '';
   position: absolute;
   left: 0;
   background: url('../../assets/icons/search.svg') 0 0/auto auto no-repeat;
   width: 24px;
   height: 24px;
}

.search {
   width: 49px;
   height: 24px;
   background: transparent;
   font-size: 16px;
   font-weight: 400;
   line-height: 24px;
   color: white;
}