.btn {
   padding: 8px 24px;
   gap: 4px;
   border-radius: 40px;
   background: #F6F6F6;
   font-size: 16px;
   font-weight: 400;
   line-height: 24px;
   color: #303030;
}

.btn:not(:first-child) {
   margin-left: 12px;
}

.btnActive {
   background: #EEEE20;
   color: #8C7E00;
}